import { Base64 } from 'js-base64';
import { KEYUTIL, KJUR, hextob64u } from 'jsrsasign';
import { authConfig } from '../constants';
// 公钥
export async function getPUBK(PUBK = '') {
    const publicKey = PUBK;
    const key = KEYUTIL.getKey(publicKey);
    let basicSecret = `Basic ${Base64.encode(`${authConfig.appId}:${authConfig.appSecret}`)}`;
    const timestamp = new Date().getTime();
    basicSecret = hextob64u(KJUR.crypto.Cipher.encrypt(`${basicSecret}\n${timestamp}`, key, undefined));
    return basicSecret;
}
