import dayjs from 'dayjs';
import { sessionKeyMap } from '..';
export function saveToken(data) {
    if (!data) {
        window.sessionStorage.clear();
        window.localStorage.clear();
        window.location.href = window.location.origin + window.location.pathname;
        return;
    }
    sessionStorage.setItem(sessionKeyMap.access_token, data.access_token);
    sessionStorage.setItem(sessionKeyMap.refresh_token, data.refresh_token);
    sessionStorage.setItem(sessionKeyMap.expires_in, String(data.expires_in));
    const expires_in = dayjs().add(data.expires_in, 's').unix();
    sessionStorage.setItem(sessionKeyMap.token_expires_time, expires_in);
    sessionStorage.setItem(sessionKeyMap.user_info, JSON.stringify(data.user_info));
    sessionStorage.setItem(sessionKeyMap.user_name, data.user_info.name);
    localStorage.setItem('storeSessionData', JSON.stringify({
        userInfo: data.user_info,
        token: data.access_token,
    }));
    sessionStorage.setItem('pt', data.pt || '');
}
