import { delToken, sessionKeyMap } from '..';
import { authConfig } from '../constants/authConfig';
import { accestokenByCode } from '../utils/accestokenByCode';
import { getPUBK } from '../utils/getPUBK';
import { initFp } from '../utils/initFp';
import { urlParams } from '../utils/urlParams';
import { axiosInstance } from '@/lib/axios';
export class Authing {
    constructor(params) {
        Object.defineProperty(this, "params", {
            enumerable: true,
            configurable: true,
            writable: true,
            value: params
        });
        const { domain, redirectUri, ...rest } = params;
        this.params = {
            domain: domain || window.location.origin,
            redirectUri: redirectUri || sessionStorage.getItem('redirectUri') || (location.origin + location.pathname),
            ...rest,
        };
        initFp();
    }
    getToken() {
        return sessionStorage.getItem('ACCESS_TOKEN');
    }
    setToken(token) {
        sessionStorage.setItem('ACCESS_TOKEN', token);
    }
    get token() {
        return this.getToken();
    }
    set token(value) {
        this.setToken(value);
    }
    async initPUBK() {
        if (this.params.PUBK)
            return this.params.PUBK;
        if (window.PUBK) {
            this.params.PUBK = window.PUBK;
            return window.PUBK;
        }
        const envData = await axiosInstance.get(`${this.params.domain}/component-shared-center/env.json`, {
            dataPath: 'data',
        });
        //  this.params =
        try {
            this.params.PUBK = envData.PUBK;
        }
        catch (error) {
        }
    }
    /**
     * 获取session里面的用户信息
     * @returns
     */
    getUserInfoFromSession() {
        return JSON.parse(sessionStorage.getItem(sessionKeyMap.user_info) || '{}');
    }
    /**
     * 跳转登录
     * @returns
     */
    async loginWithRedirect() {
        this.saveLocalSearchParams();
        if (urlParams.get('code'))
            return false;
        if (this.params.loginWithRedirectHandle) {
            await this.params.loginWithRedirectHandle({
                ...this.params,
            });
            return;
        }
        sessionStorage.setItem('auth', 'lj');
        window.location.href
            = `${this.params.domain}/auth/oauth/authorize?response_type=code&client_id=${authConfig.appId}&redirect_uri=${this.params.redirectUri}`;
    }
    async logoutWithRedirect() {
        const userInfo = JSON.parse(sessionStorage.getItem(sessionKeyMap.user_info) || '{}');
        const userId = userInfo.id;
        const fp = localStorage.getItem('fp');
        const pt = sessionStorage.getItem('pt') || 1;
        try {
            window.sessionStorage.setItem('noclose', 'noclose');
            delToken();
            await axiosInstance.get(`${this.params.domain}/auth/logout?ct=1&userId${userId}&fp=${fp}&pt=${pt}`);
            this.loginWithRedirect();
        }
        catch (error) {
            window.location.href = `${this.params.domain}/auth/logout?ct=1&userId${userId}&fp=${fp}&pt=${pt}`;
        }
    }
    /**
     * 获取登录态
     * @returns false | true
     */
    static getLoginState() {
        if (sessionStorage.getItem(sessionKeyMap.access_token)
            && sessionStorage.getItem(sessionKeyMap.user_info))
            return true;
        return false;
    }
    /**
     * 判断当前 URL 是否为 Authing 登录回调 URL，
     * @returns false | true
     */
    static isRedirectCallback() {
        if (urlParams.get('code'))
            return true;
        return false;
    }
    /**
     * 获取秘钥
     * @returns
     */
    async getBasicSecret() {
        await this.initPUBK();
        return await getPUBK(this.params.PUBK);
    }
    /**
     * 解析code的默认行为(可信)
     * @param code
     * @returns
     */
    async defaultResolveCodeHandle(code) {
        // 获取到code
        try {
            const userInfo = await accestokenByCode(code, this.params.basicSecret, {
                ...this.params,
            });
            const search = sessionStorage.getItem('lastSearch') || '';
            // 去除地址栏上的
            const url = new URL(this.params.redirectUri.endsWith('/') ? `${this.params.redirectUri}${search}` : `${this.params.redirectUri}/${search}`);
            url.searchParams.delete('code');
            window.history.replaceState({}, '', url);
            window.location.reload();
            return userInfo;
        }
        catch (error) {
            this.loginWithRedirect();
        }
    }
    saveLocalSearchParams() {
        sessionStorage.setItem('lastSearch', window.location.search || '');
    }
    async handleRedirectCallback() {
        await initFp();
        const basicSecret = await this.getBasicSecret();
        this.params.basicSecret = basicSecret;
        if (Authing.getLoginState())
            return this.getUserInfoFromSession();
        if (urlParams.get('code')) {
            const code = urlParams.get('code');
            await this.initPUBK();
            if (this.params.resolveCodeHandle) {
                return this.params.resolveCodeHandle(code, {
                    ...this.params,
                });
            }
            return this.defaultResolveCodeHandle(code || '');
        }
        return await this.loginWithRedirect();
    }
}
