import { Authing, TjSSoAuthing, isMobileEnv, loginSuccess } from "./TjSSo";
function start() {
    const isMobile = isMobileEnv();
    if (isMobile) {
        window.addEventListener("flutterInAppWebViewPlatformReady", () => {
            const tjAuthing = TjSSoAuthing({
                domain: window.location.hostname === "localhost"
                    ? "http://dev-pm.crccit.com:31006"
                    : window.location.origin,
                redirectUri: window.location.origin,
            });
            const afterRedirectCallback = () => {
                if (Authing.getLoginState()) {
                    // 移动端,执行脚本
                    if (isMobile) {
                        loginSuccess({
                            access_token: sessionStorage.getItem("ACCESS_TOKEN"),
                            expires_in: sessionStorage.getItem("EXPIRES_IN"),
                            fp: sessionStorage.getItem("fp"),
                            pt: sessionStorage.getItem("pt"),
                            refresh_token: sessionStorage.getItem("REFRESH_TOKEN"),
                            user_info: sessionStorage.getItem("USER_INFO"),
                        });
                    }
                }
            };
            tjAuthing.handleRedirectCallback().then(() => {
                afterRedirectCallback();
            });
        });
    }
    else {
        const tjAuthing = TjSSoAuthing({
            domain: window.location.hostname === "localhost"
                ? "http://dev-pm.crccit.com:31006"
                : window.location.origin,
            redirectUri: window.location.origin,
        });
        const afterRedirectCallback = () => {
            if (Authing.getLoginState()) {
                // pc 端 回到首页
                window.history.replaceState(null, "", `${window.location.origin}/portal`);
                window.location.reload();
            }
        };
        tjAuthing.handleRedirectCallback().then(() => {
            afterRedirectCallback();
        });
    }
}
start();
