/**
 * 请求授权服务器获取token
 * @param code
 */
import { FpKey } from '..';
import { axiosInstance } from '../../../axios/';
import { saveToken } from './saveToken';
// const config = action.getActionParams()
export async function accestokenByCode(params, basicSecret, aParams) {
    try {
        const res = await axiosInstance(`${aParams?.domain}/auth/oauth/token`, {
            placeholderData: {},
            data: {
                ct: sessionStorage.getItem('ct') || 1,
                grant_type: 'authorization_code',
                code: params,
                redirect_uri: aParams?.redirectUri,
                fp: localStorage.getItem(FpKey),
                v: 1,
            },
            dataPath: 'data',
            headers: {
                'Content-Type': 'multipart/form-data',
                // Authorization: 'Basic ' + BasicSecret,
                'Authorization': basicSecret,
            },
            method: 'post',
        });
        if (res.code !== 200) {
            sessionStorage.clear();
            return Promise.reject(res);
        }
        const data = res?.data;
        saveToken(data);
        return data?.user_info;
    }
    catch (error) {
        sessionStorage.clear();
        return Promise.reject(error);
    }
}
