import { sessionKeyMap } from '../constants';
export async function delToken() {
    sessionStorage.removeItem(sessionKeyMap.access_token);
    sessionStorage.removeItem(sessionKeyMap.refresh_token);
    sessionStorage.removeItem(sessionKeyMap.expires_in);
    sessionStorage.removeItem(sessionKeyMap.token_expires_time);
    sessionStorage.removeItem(sessionKeyMap.user_info);
    sessionStorage.removeItem(sessionKeyMap.user_name);
    sessionStorage.removeItem('selectedKeys');
    sessionStorage.removeItem('openKeys');
    localStorage.removeItem('dict_code');
    // sessionStorage.clear();
}
