import axios from 'axios';
import { get } from 'lodash-es';
export function genAxiosInstance() {
    const axiosInstance = axios.create();
    axiosInstance.interceptors.request.use((req) => {
        req.headers.set('fp', localStorage.getItem('fp') || '');
        req.headers.set('pt', sessionStorage.getItem('pt') || '1');
        req.headers.set('ct', localStorage.getItem('ct') || '1');
        if (sessionStorage.getItem('ACCESS_TOKEN')) {
            req.headers.set('Authorization', `Bearer ${sessionStorage.getItem('ACCESS_TOKEN')}`);
        }
        return req;
    });
    axiosInstance.interceptors.response.use((res) => {
        const config = res.config;
        if (res && res.headers && res.headers.access_token) {
            const access_token = res.headers.access_token;
            const data = JSON.parse(decodeURIComponent(access_token));
            sessionStorage.setItem('ACCESS_TOKEN', data.access_token);
            sessionStorage.setItem('USER_INFO', JSON.stringify(data.user_info));
            sessionStorage.setItem('USER_NAME', data.user_info.name);
            sessionStorage.setItem('EXPIRES_IN', data.expires_in);
            sessionStorage.setItem('REFRESH_TOKEN', data.refresh_token);
            sessionStorage.setItem('sessionId', data.refresh_token);
            localStorage.setItem('storeSessionData', JSON.stringify({
                userInfo: data.user_info,
                token: data.access_token,
            }));
        }
        const code = get(res, 'data.code');
        if (code === 401) {
            //
            sessionStorage.clear();
            window.location.reload();
        }
        return get(res, config.dataPath !== undefined ? config.dataPath : 'data.data', config.placeholderData);
    });
    return axiosInstance;
}
export const axiosInstance = genAxiosInstance();
