class BridgeAction {
    /**
     * 获取桥接对象
     * @returns
     */
    getBirdgeObj() {
        if (!window.flutter_inappwebview)
            throw new Error('请在app环境下使用');
        return window.flutter_inappwebview;
    }
    /**
     *
     * @returns 是否是app环境
     */
    isMobileEnv() {
        return !!window.flutter_inappwebview;
    }
    /**
     *
     * @returns 获取登录信息
     */
    getBridgeToken() {
        return new Promise((resolve) => {
            this.getBirdgeObj()
                ?.callHandler('getLoginInfo')
                .then((result) => {
                resolve(result);
            });
        });
    }
    /**
     *
     * @returns 设置桥接对象到session
     */
    async setBridgeInfoToSession() {
        try {
            const loginInfo = (await this.getBridgeToken());
            let _userInfo;
            if (loginInfo && loginInfo.userInfo) {
                const userInfo = loginInfo.userInfo;
                _userInfo = {
                    companyName: userInfo.company?.name ?? '',
                    defaultCompId: userInfo.company?.id ?? '',
                    employeeId: userInfo.employeeId,
                    employeeName: userInfo.employeeName,
                    id: userInfo.id,
                    loginName: userInfo.loginName,
                    mobile: userInfo.mobile,
                    name: userInfo.name,
                };
                sessionStorage.setItem('ACCESS_TOKEN', loginInfo.accessToken);
                sessionStorage.setItem('pt', loginInfo.pt);
                if (_userInfo)
                    sessionStorage.setItem('USER_INFO', JSON.stringify(_userInfo));
            }
            if (loginInfo && loginInfo.fp)
                localStorage.setItem('fp', loginInfo.fp);
            if (loginInfo && loginInfo.ct)
                sessionStorage.setItem('ct', loginInfo.ct);
            return Promise.resolve({
                ..._userInfo,
                client_id: loginInfo.client_id,
                client_secret: loginInfo.client_secret,
            });
        }
        catch (error) {
            return Promise.reject(error);
        }
        // return _userInfo;
    }
    async loginSuccess(userInfo) {
        return await this.getBirdgeObj()?.callHandler('loginSuccess', userInfo);
    }
}
export const bridgeAction = new BridgeAction();
