import FingerprintJS from '@fingerprintjs/fingerprintjs';
import { FpKey } from '../constants/storageKey';
import { bridgeAction } from './bridgeAction';
export async function initFp() {
    if (bridgeAction.isMobileEnv())
        return bridgeAction.setBridgeInfoToSession();
    const localFp = localStorage.getItem(FpKey);
    if (!localFp) {
        const fpPromise = FingerprintJS.load();
        const fp = await fpPromise;
        const result = await fp.get();
        const { deviceMemory, ...components } = result.components;
        const visitorId = FingerprintJS.hashComponents(components);
        localStorage.setItem('fp', visitorId);
        return visitorId;
    }
    else {
        return localFp;
    }
}
