import { Authing } from "../core";
import { bridgeAction } from "../core/utils/bridgeAction";
import { saveToken } from "../core/utils/saveToken";
import { axiosInstance } from "@/lib/axios";
export { Authing } from "../core";
export function TjSSoAuthing({ domain = "", redirectUri = "" } = {}) {
    return new Authing({
        manual: true,
        domain: domain || location.origin,
        redirectUri: redirectUri || location.origin,
        resolveCodeHandle: async (code, params) => {
            try {
                const userInfo = await axiosInstance({
                    url: `/auth/oauth/oidcToken`,
                    method: "post",
                    headers: {
                        accept: "application/json",
                        "content-type": "application/x-www-form-urlencoded",
                        pt: sessionStorage.getItem("pt") || 1,
                        // Authorization: 'Basic ' + BasicSecret,
                        Authorization: params.basicSecret,
                    },
                    dataPath: "data.data",
                    data: {
                        ct: sessionStorage.getItem("ct") || 1,
                        code,
                        fp: localStorage.getItem("fp"),
                        v: 1,
                    },
                });
                if (userInfo) {
                    if (bridgeAction.isMobileEnv())
                        bridgeAction.loginSuccess(userInfo);
                    saveToken(userInfo);
                }
                else {
                    saveToken(null);
                }
            }
            catch (error) {
                saveToken(null);
            }
        },
        loginWithRedirectHandle: async () => {
            const isMobile = bridgeAction.isMobileEnv();
            sessionStorage.setItem("auth", "cu");
            const params = isMobile ? "" : `?ct=1`;
            //   window.location.href;
            fetch(`${domain}/auth/getOidcUrl${params}`, {
                method: "get",
            }).then((response) => {
                return response.json().then((res) => {
                    // console.log(res, "res");
                    window.location.href = res.data;
                    // 本地调试,注意注释
                    // window.location.href =
                    //   "https://g1openid.crcc.cn/oauth/authorize?response_type=code&client_id=ccit-pm&scope=openid&redirect_uri=http://localhost:5173/tjSSo.html";
                });
            });
        },
    });
}
export function LjSSoAuthing({ domain = "", redirectUri = "" } = {}) {
    return new Authing({
        manual: true,
        domain: domain || location.origin,
        redirectUri: redirectUri || location.origin,
        resolveCodeHandle: undefined,
        loginWithRedirectHandle: undefined,
    });
}
export function loginSuccess(info = {}) {
    if (bridgeAction.isMobileEnv()) {
        // 调用loginSuccess
        bridgeAction.loginSuccess(info);
    }
}
export function isMobileEnv() {
    return bridgeAction.isMobileEnv();
}
